"use client"; 

import Image from 'next/image';
import { useLayoutEffect, useRef } from 'react';
import gsap from 'gsap';
import Countdown from '@/components/Countdown'; // Countdown component එක import කිරීම

export default function Home() {
  const comp = useRef(null); 

  useLayoutEffect(() => {
    let ctx = gsap.context(() => {
      const t1 = gsap.timeline();
      t1.from("#intro-slider", {
        xPercent: "-100",
        duration: 1.3,
        delay: 0.3,
      })
      .from(["#title-1", "#title-2", "#title-3"], {
        opacity: 0,
        y: "+=30",
        stagger: 0.5,
      })
      .to(["#title-1", "#title-2", "#title-3"], {
        opacity: 0,
        y: "-=30",
        delay: 0.3,
        stagger: 0.5,
      })
      .to("#intro-slider", {
        xPercent: "-100",
        duration: 1.3,
      })
      .from("#welcome", {
        opacity: 0,
        duration: 0.5,
      });

    }, comp);

    return () => ctx.revert(); 
  }, []);


  return (
    // මුල් පිටුවේ ප්‍රධාන container එක
    <div className="bg-black" ref={comp}>
      {/* Animation Slider */}
      <div id="intro-slider" className="h-full w-full fixed top-0 left-0 z-20 bg-black flex flex-col gap-10 items-center justify-center">
        <h1 className="text-5xl md:text-8xl font-bold text-white" id="title-1">FIRST EVER</h1>
        <h1 className="text-5xl md:text-8xl font-bold text-white" id="title-2">MINISTRY RECOGNIZED</h1>
        <h1 className="text-5xl md:text-8xl font-bold text-white" id="title-3">ICT DAY</h1>
      </div>

      {/* Hero Section */}
      <main id="welcome" className="relative flex flex-col items-center justify-center w-full h-screen bg-black overflow-hidden z-10">
        <div className="absolute top-0 left-0 w-full h-full">
          <Image
            src="/hero-background.jpg"
            alt="Futuristic cyberpunk background"
            layout="fill"
            objectFit="cover"
            className="opacity-30"
          />
        </div>
        <div className="relative z-10 flex flex-col items-center text-center text-white p-5">
          <div className="mb-6">
            <Image
              src="/nexentia-logo.png"
              alt="Nexentia '25 Logo"
              width={700}
              height={175}
              priority
            />
          </div>
          <h1 className="text-xl md:text-3xl font-bold tracking-widest uppercase mb-4 text-cyan-300">
    FIRST EVER MINISTRY OF EDUCATION REGOGNIZED ICT DAY IN SRI LANKA
</h1>
<p className="text-lg md:text-xl font-light mb-12">
    BY ST. JOSEPH'S GIRLS' SCHOOL
</p>
<div className="flex flex-col md:flex-row gap-6">
    <button className="px-8 py-3 bg-transparent border-2 border-purple-500 text-purple-500 font-semibold rounded-lg hover:bg-purple-500 hover:text-white transition-all duration-300 ease-in-out transform hover:scale-105 shadow-[0_0_15px_rgba(168,85,247,0.5)]">
        SCHOOL REGISTRATION
    </button>
    <button className="px-8 py-3 bg-purple-500 text-white font-semibold rounded-lg hover:bg-purple-600 transition-all duration-300 ease-in-out transform hover:scale-105 shadow-[0_0_15px_rgba(168,85,247,0.8)]">
        EXPLORE COMPETITIONS!
    </button>
</div>
        </div>
      </main>
    
      <Countdown />

    </div>
  );
}