// src/components/Countdown.tsx

"use client"; 
import { useEffect, useState } from 'react';

const TimeCard = ({ value, unit }: { value: number; unit: string }) => {
    return (
        <div className="flex flex-col items-center">
            <div className="text-5xl md:text-7xl font-bold text-purple-400 bg-gray-900/50 p-4 md:p-6 rounded-lg shadow-lg border border-purple-800/50">
                {String(value).padStart(2, '0')}
            </div>
            <div className="text-sm md:text-lg uppercase tracking-widest mt-3 text-gray-400">
                {unit}
            </div>
        </div>
    );
}

const Countdown = () => {
    // **වැදගත්:** මෙතනට ඔබගේ තරඟය ආරම්භ වන දිනය සහ වේලාව යොදන්න
    const targetDate = new Date('2025-07-20T09:00:00');

    const calculateTimeLeft = () => {
        const difference = +targetDate - +new Date();
        let timeLeft = {
            days: 0,
            hours: 0,
            minutes: 0,
            seconds: 0
        };

        if (difference > 0) {
            timeLeft = {
                days: Math.floor(difference / (1000 * 60 * 60 * 24)),
                hours: Math.floor((difference / (1000 * 60 * 60)) % 24),
                minutes: Math.floor((difference / 1000 / 60) % 60),
                seconds: Math.floor((difference / 1000) % 60)
            };
        }
        return timeLeft;
    };

    const [timeLeft, setTimeLeft] = useState(calculateTimeLeft());

    useEffect(() => {
        
        const timer = setTimeout(() => {
            setTimeLeft(calculateTimeLeft());
        }, 1000);

        return () => clearTimeout(timer);
    });

    return (
        <section className="w-full bg-black py-20 text-white text-center">
            <div className="container mx-auto px-4">
                <h2 className="text-4xl md:text-5xl font-bold uppercase tracking-wider text-purple-400">
                    OPERATION IN MOTION 
                </h2>
                <p className="text-xl md:text-2xl mt-4 mb-12 text-gray-300">
                    CLAIM YOUR PLACE NOW!
                </p>

                <div className="flex justify-center gap-4 md:gap-8">
                    <TimeCard value={timeLeft.days} unit="Days" />
                    <TimeCard value={timeLeft.hours} unit="Hours" />
                    <TimeCard value={timeLeft.minutes} unit="Minutes" />
                    <TimeCard value={timeLeft.seconds} unit="Seconds" />
                </div>
            </div>
        </section>
    );
};

export default Countdown;